.main-cotainer {
    min-height: 100%;
    @include experimental-value(min-height, calc(100%));
}

.dashboard-container {
    .dashboard-content {
        @include padding(50 60);
        @include margin-left(260);
        @include margin-top(80);

        &.padding-sm {
            @include padding(40 15);
        }
    }
}

.sort-input-field {
    .s-btn {
        width: 40px;
        @include fontsize(18);
        @include margin-right(5);
        height: 40px;
        text-align: center;
        display: $ib;
        background-color: $white;
        @include border(1, solid, #b3b3b3);
        line-height: 40px;
        vertical-align: middle;
        cursor: pointer;
    }
    .select-control {
        max-width: 210px;
        display: $ib;
        vertical-align: middle;
    }
    .sort-field-control {
        width: 210px;
        height: 40px;
        @include border(1, solid, #b3b3b3);
        background-color: $white;
        @include padding(0 10);
        @include fontsize(14);
        font-weight: 500;
    }
}


.page-title {
    text-align: center;
    @include fontsize(22);
    font-weight: 400;
    @include margin-top(10);
}

.connection-box {
    background-color: $theme;
    text-align: center;
    @include margin-bottom(20);
    @include border-round(4);
    a {
        @include padding(40 10);
        display: $b;
    }
    h3 {
        color: $white;
        @include fontsize(20);
        font-weight: 500;
    }

    &.c-1 {
        background-color: $color-connetion-01;
    }
    &.c-2 {
        background-color: $color-connetion-02;
    }
    &.c-3 {
        background-color: $color-connetion-04;
    }
    &.c-4 {
        background-color: $color-connetion-03;
    }
    &.c-5 {
        background-color: $color-connetion-05;
    }

    &.c-6 {
        background-color: $color-connetion-06;
    }

    &.c-7 {
        background-color: $color-connetion-07;
    }



    &:hover {
        @include opacity(0.9);
    }
}



.c-field {
    @include margin-bottom(50);
    .c-field-control {
        width: 100%;
        background-color: transparent;
        font-weight: 100;
        border-radius: 4px;
        font-family: $font-family;
        @include border(1, solid, #b8b8b8);
        @include padding(10 15);
        height: 48px;
        @include fontsize(15);
        font-weight: 400;
        color: $color-text-01;
        outline: none !important;
        text-overflow: ellipsis;

        &::-webkit-input-placeholder {
            /* Chrome/Opera/Safari */
            color: lighten($color-text-01, 15%);
            opacity: 1;
        }
        &::-moz-placeholder {
            /* Firefox 19+ */
            color: lighten($color-text-01, 15%);
            opacity: 1;
        }
        &:-ms-input-placeholder {
            /* IE 10+ */
            color: lighten($color-text-01, 15%);
            opacity: 1;
        }
        &:-moz-placeholder {
            /* Firefox 18- */
            color: lighten($color-text-01, 15%);
            opacity: 1;
        }

        &:focus {
            border-color: darken(#b8b8b8, 20%);
        }
    }
    textarea.c-field-control {
        resize: none;
        height: auto !important;
    }

    &.c-bg-color {
        .c-field-control {
            background-color: $color-bg-01;
        }
    }

    &.c-md {
        .c-field-control {
            @include fontsize(14);
        }

        tags-input {
            .tags {
                @include fontsize(14);

                .input {
                    @include fontsize(14);
                }
            }
        }
    }
    &.c-lh {
        .c-field-control {
            height: 55px;
        }

        tags-input {
            .tags {
                min-height: 55px;

                .input {
                    height: 30px;
                }
            }
        }
    }

    &.c-sh {
        .c-field-control {
            height: 40px;
        }

        tags-input {
            .tags {
                min-height: 40px;

                .input {
                    height: 30px;
                }
            }
        }
    }

    &.c-shadow {
        .c-field-control {
            @include box-shadow(inset 0 3px 14px rgba(0, 0, 0, .11));
        }
    }
    &.c-b-none {
        .c-field-control {
            border: 0;
            border-radius: 0;
        }
    }
    &.c-txt-medium {
        .c-field-control {
            font-weight: 500;
        }
    }
    &.c-txt-small {
        .c-field-control {
            @include fontsize(14);
        }
    }

    &.c-b-lg {
        @include margin-bottom(60);
    }

    &.c-b-sm {
        @include margin-bottom(30);
    }

    &.c-b-xsm {
        @include margin-bottom(10);
    }


    .c-has-icon {
        @include pos(rel, null);
        .c-field-control {
            @include padding-right(20);
        }
        .icon-addon {
            @include pos(ab, 50% 20px null null);
            @include fontsize(20);
            @include translatepos(0, -50%);
            color: $color-text-01;
            cursor: pointer;
        }
    }

    .select-control {
        &:after {
            right: 20px;
            @include arrow(bottom, #444444, 6px);
        }
    }


    tags-input {
        .host {
            @include margin(0);
        }
        .tags {
            @include border(1, solid, #b8b8b8);
            @include padding(10 15);
            min-height: 48px;
            @include fontsize(16);
            font-weight: 400;
            color: $color-text-01;
            outline: none !important;
            border-radius: 4px;
            @include box-shadow(inset 0 3px 14px rgba(0, 0, 0, .11));

            .input {
                margin: 0;
                @include fontsize(16);
                outline: none !important;
                width: auto !important;
                color: $color-text-01;
                font-weight: 400;
                font-family: $font-family;
                &::-webkit-input-placeholder {
                    /* Chrome/Opera/Safari */
                    color: lighten($color-text-01, 10%);
                    opacity: 1;
                }
                &::-moz-placeholder {
                    /* Firefox 19+ */
                    color: lighten($color-text-01, 10%);
                    opacity: 1;
                }
                &:-ms-input-placeholder {
                    /* IE 10+ */
                    color: lighten($color-text-01, 10%);
                    opacity: 1;
                }
                &:-moz-placeholder {
                    /* Firefox 18- */
                    color: lighten($color-text-01, 10%);
                    opacity: 1;
                }
            }

            &.focused {
                -webkit-appearance: none !important;
                -webkit-box-shadow: none;
                @include box-shadow(inset 0 3px 14px rgba(0, 0, 0, .11));
                outline: none !important;
            }
        }
    }


    label {
        @include fontsize(18);
        font-weight: 400;
        @include padding-bottom(10);
        color: $color-text-01;
    }
}

.rating-opt-field {
    .rating-container {
        @include fontsize(24);
        @include margin-bottom(10);
    }
}



tags-input {
    .tag-list {
        .tag-item {
            background: transparent;
            border: 0;
            @include fontsize(16);
            font-weight: 400;
            font-family: $font-family;
            span {
                display: $ib;
                vertical-align: middle;
            }
            .remove-button {
                width: 16px;
                height: 16px;
                @include border(1, solid, #444444);
                @include circle;
                display: $ib;
                vertical-align: middle;
                text-align: center;
                line-height: 15px;
            }
        }
    }
}


.back-btn {
    @include fontsize(20);
    font-weight: 400;
    color: $color-text-01;

    .ico {
        @include margin-right(12);
    }
}

.rating-container {
    @include fontsize(16);
    .star-uncheck {
        color: #bab9b9;
    }
    .star-checked {
        color: #ffcd00;
    }
}